import React, { Fragment } from "react";

import {
    Dialog, DialogTitle, DialogContent,
    DialogActions, Button, withMobileDialog,
    Typography, Grid, DialogContentText
} from "@material-ui/core";

import moment from "moment";

const NewAlertsModal = props => {
    const {
        newAlerts, isOpen,
        setNewAlertModalOpen, handleStopPlayingSoundNotif
    } = props;
    
    const handleClose = () => {
        setNewAlertModalOpen(false);
        handleStopPlayingSoundNotif();
    };

    return (
        <Dialog 
            fullWidth
            open={isOpen} disableBackdropClick
            onClose={handleClose} 
            aria-labelledby="form-dialog-title"
        >
            <DialogTitle id="form-dialog-title">New alerts for validation</DialogTitle>
            <DialogContent>
                <DialogContentText>
                    Validate the following alert triggers. Acknowledge all alerts on candidate alerts&apos;
                    validation dialog box.
                </DialogContentText>

                {
                    newAlerts && newAlerts.map((row, index) => {
                        const { site_code, trigger_list_arr } = row;

                        return (
                            <Grid container spacing={2} key={site_code} style={{ textAlign: "center" }}>
                                <Typography 
                                    gutterBottom variant="h6"
                                    component={Grid} item xs={12}
                                >
                                    Site Code: {site_code.toUpperCase()}
                                </Typography>
                                {
                                    trigger_list_arr.length > 0 && trigger_list_arr.map((trigger_row, trigger_index) => {
                                        const { trigger_alert_level, ts_updated } = trigger_row;
                                        return (
                                            <Fragment key={trigger_index}>
                                                <Grid item xs={4}>
                                                    <Typography color="textSecondary">
                                                        Trigger
                                                    </Typography>
                                                    <Typography variant="body2" component="p">
                                                        {trigger_alert_level}
                                                    </Typography>
                                                </Grid>
                                                <Grid item xs={8}>
                                                    <Typography color="textSecondary">
                                                        Trigger timestamp
                                                    </Typography>
                                                    <Typography variant="body2" component="p">
                                                        {moment(ts_updated).format("DD MMMM YYYY, HH:mm")}
                                                    </Typography>
                                                </Grid>
                                            </Fragment>
                                        );
                                    })
                                }
                            </Grid>
                        );
                    })
                }
            </DialogContent>
            <DialogActions>
                <Button color="primary" onClick={handleClose}>
                    Close
                </Button>
            </DialogActions>
        </Dialog>
    );
};


export default withMobileDialog()(NewAlertsModal);
